import PageItemSelect from "../PageItemSelect/PageItemSelect"
import FavoriteCard from "./FavoriteCard/FavoriteCard"

function Favorite (){
    return(
        <section className="favorite">
            <PageItemSelect select="Избранное"/>
            <h2 className="favorite__title">Избранное</h2>
            <ul className="favorite__list">
                <FavoriteCard title="Бежевая шуба" price="4200 грн" size={[ "xs", "s", "l"]} color={["#FFFFFF" , "#6F83A4", "#F1DDAA"]}/>
                

            </ul>
        </section>
    )
}
export default Favorite