import errorCloud from "../.././img/not_found/cloud.svg"
function NotFound (){
    return(
        <section className="not__found">
            <img className="not__found__img" src={errorCloud} alt="" />
            <h2 className="not__found__title">Страницы, которую вы ищете нет на нашем сайте.</h2>
            <h3 className="not__found__subtitle">Пожалуйста воспользуйтесь навигацией в шапке сайта.</h3>
        </section>
    )
}
export default NotFound