import React from "react"
import PageItemSelect from "../PageItemSelect/PageItemSelect"
import CardItem from "./CardItem/CardItem"
import { Link } from "react-router-dom"
import visa from "../.././img/cart/visa.png"
import logo24 from "../.././img/cart/24.png"



function Cart (){

    return(
        <section className="cart">
            <PageItemSelect select="Корзина"/>
            <div className="you__order">
                <h2 className="you__order__title">Ваш заказ</h2>
                <ul className="cart__list">
                    <CardItem price={3150}/>
                    <CardItem price={3150}/>
                    <CardItem price={3150}/>
                </ul>
                <h2 className="price__sum">К оплате: <p className="total__value">{0} грн</p></h2>
            </div>
            <div className="making_an_order">
                <h2 className="making_an_order__title">Оформление заказа</h2>
                <div className="making_an_order__info">
                    <form className="cart__form" action="">
                        <h3 className="personal__data__title">Персональные данные:</h3>
                        <div className="personal__data__wrapper">
                            <input className="cart__input personal_data__input" type="text" placeholder="Ваше имя*"/>
                            <input className="cart__input personal_data__input" type="text" placeholder="Ваша фамилия*"/>
                            <input className="cart__input personal_data__input" type="email" placeholder="Ваш e-mail*"/>
                            <input className="cart__input personal_data__input" type="tel" placeholder="Ваш телефон*"/>
                        </div>
                        <h3 className="delivery_method__title">Способ доставки:</h3>
                        <div className="delivery_method__info">
                            <div className="ukraine">
                                <h4 className="delivery_method__location">По Украине:</h4>
                                <ul className="delivery_method__location__list">
                                    <li className="delivery_method__location__item">
                                        <input type="checkbox" name="" id="" />
                                        <label className="delivery_method__location__label" htmlFor="">Самовывоз - вул. Большая Васильковская 14(м. Льва Толстого)</label>

                                    </li>
                                    <li className="delivery_method__location__item">
                                        <input type="checkbox" name="" id="" /> 
                                        <label className="delivery_method__location__label" htmlFor="">Новая Почта</label>
                                    </li>
                                </ul>
                            </div>
                            <div className="global">
                                <h4 className="delivery_method__location">Международная доставка:</h4>
                                <ul className="delivery_method__location__list">
                                    <li className="delivery_method__location__item">
                                        <input type="checkbox" name="" id="" />
                                        <label className="delivery_method__location__label" htmlFor="">Украпочта / 1-3 недели / 30$</label>

                                    </li>
                                    <li className="delivery_method__location__item">
                                        <input type="checkbox" name="" id="" /> 
                                        <label className="delivery_method__location__label" htmlFor="">DHL / 3-7 дней / 60$</label>
                                    </li>
                                </ul>
                            </div>
                        </div>
                        <div className="delivery_address">
                            <h3 className="delivery_address__title">Адрес доставки:</h3>
                            <input className="cart__input input_address" type="text" placeholder="Город*"/>
                            <input className="cart__input input_address" type="text" placeholder="Отделение почты*"/>
                        </div>
                        <div className="payment_method">
                            <h3 className="payment_method__title">Вы можете оплатить покупку одним из ниже перечисленных способов:</h3>
                            <ul className="payment_method__list">
                                <li className="payment_method__item">
                                    <input type="checkbox" name="" id="" />
                                    <label htmlFor="">Полная предоплата через Приват 24 <img className="logo24" src={logo24} alt="" /></label>
                                </li>
                                <li className="payment_method__item"> 
                                    <input type="checkbox" name="" id="" />
                                    <label htmlFor="">Денежным переводом  Visa/MasterCard <img className="visa" src={visa} alt="" /></label>
                                </li>
                                <li className="payment_method__item">
                                    <input type="checkbox" name="" id="" />
                                    <label htmlFor="">Наложенным платежом в отделении Новой Почты</label>
                                </li>
                            </ul>
                        </div>
                        <div className="bonuses_account">
                            <h3 className="bonusess_account__title">Использование бонусного счёта:</h3>
                            <input className="cart__input bonuses_account__input" type="text" placeholder="Сумма списания бонусов*"/>
                        </div>
                    </form>
                    <div className="result">
                        <ul className="nav__list">
                            <li className="nav__item nav__item__login"><Link to="/Page/PesonalAccount/">Войти в личный кабинет</Link></li>
                            <li className="nav__item"><Link to="/Page/Delivery/Delivery">УСЛОВИЯ ДОСТАВКИ</Link></li>
                            <li className="nav__item"><Link to="/Page/Refund/Refund">УСЛОВИЯ ОБМЕНА И ВОЗВРАТА</Link></li>
                            <li className="nav__item"><Link to="/Page/Delivery/Delivery">ИНФОРМАЦИЯ ОБ ОПЛАТЕ</Link></li>
                        </ul>
                        <ul className="result__info">
                            <li className="info__item">
                                <h5 className="info__item__title">ДОСТАВКА:</h5>
                                <span className="info__item_value">По тарифам перевозчика</span>
                            </li>
                            <li className="info__item">
                                <h5 className="info__item__title">БОНУСЫ:</h5>
                                <span className="info__item_value">-69 грн</span>
                            </li>
                            <li className="info__item">
                                <h5 className="info__item__title">ИТОГО:</h5>
                                <span className="info__item_value">15250 грн</span>
                            </li>
                        </ul>
                        <button className="cart__form__submite">Оформить заказ</button>
                        <p className="cart__form__description">
                            Нажимая на кнопку «оплатить заказ», я принимаю условия <Link>публичной оферты</Link> и <Link> политики конфиденциальности</Link>
                        </p>
                    </div>
                </div>  
            </div>
        </section>
    )
}
export default Cart