import { useEffect } from "react";
import OrderTab from "./OrderTab";
function OrderHistory (props){
    const { load, setLoad } = props;
    useEffect(() => {
        setLoad(true);
        return () => {
          setLoad(false);
        };
      }, []);

    return(
        <ul className="order__history">
            <OrderTab tabState={false} data="№ 718 от 28.09.21" status="Отменён" sum="13682 грн"/>
            <OrderTab tabState={false} data="№ 328 от 10.07.21" status="Выполнен" sum="7650 грн"/>
            <OrderTab tabState={true} data="№ 235 от 03.05.21" status="Выполнен" sum="7650 грн"/>
            <OrderTab tabState={false} data="№ 213 от 01.05.21" status="Выполнен" sum="7650 грн"/>
        </ul>
    )
}
export default OrderHistory